<template>
  <div class="input-wrapper">
    <!-- <h1>这里是输入框组件的 h1</h1> -->
    <!-- 每当我们的这个 input 框接收到输入
    都要进行一次判断, 看看合不合法
    可以绑定 input 事件 -->

      <!-- 这里 input 是一个输入框
      我希望如果输入发生错误的时候它的样式被改变
      方法是动态 v-bind 绑定一个 class 进来 -->

      <!-- 为什么绑定 input 事件
      不是绑定 change / blur 事件
      是因为每次输入我们希望能够马上改变下划线样式, 不是等到失去焦点再判断 -->


      <!-- 以前我们的写法是绑定一个变量, 直接以字符串变量作为 class
      <input type="text" :class="isValid">
      这样子只会绑定一个字符串 true / false
      如果想根据布尔值动态添加 class 可以使用 对象的形式声明 -->
      <!-- 这个对象里面是 key : value 键值对
      其中 key 是准备添加的 class 
      value 是用来判断是否应该添加的布尔值 -->
      <!-- 现在我们希望 isValid 为 false, 自动添加 err class -->
      <!-- 假设我希望, 如果 isValid 为 true 则添加 success 的 class  -->
      <!-- 现在input 已经可以根据 isValid 动态改变 class 接下来只需要 给 err
      class 添加特定的 样式即可 -->
      <input
        :class="{
          err: !isValid,
          success: isValid
        }"
        :type="type" 
        :placeholder="placeholder"
        @input="checkValue"
        @blur="showAlert"
      >
      <!-- 监控失去焦点事件
      如果失去焦点(输入完毕)
      这个 input 的数据还是不合法
      需要弹窗提醒 -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      isValid: false
    }
  },
  // 以前我们创建局部组件时候用到的
  // 除了 template 以外的所有属性
  // 全都放在 script export default 里面
  // props 接收父组件传值
  props: ['type', 'placeholder', 'rule', 'errMsg'],
  methods: {
    // 这是一个对象, 里面的每个属性都是一个函数
    checkValue(event) {
      // console.log('输入框的值发生了变化');
      // 这里可以获取到输入框的值 event.target.value 
      // event 代表事件
      // target 代表触发事件的dom
      // value 代表这个输入框的值
      // console.log(event.target.value);
      // 假如我们现在想要验证这个输入框的数据
      // 就是使用正则表达式,判断是否合法,然后返回一个true/false 即可
      // const regExp = /.{3}/; 代表任意字符三个以上才合法
      // const regExp = /.{3,10}/; 代表任意字符三个到十个以内都是合法 
      
      // 这里的正则是写死的, 其实这个输入框可能的使用场景非常多
      // 比如用来输入用户名, 用来输入密码, 用来输入手机号
      // 不同作用下, 这个输入框的验证正则应该是不一样
      // 传参 像 type placeholder 一样
      // const regExp = /^.{3,10}$/;
      // 不可以再调用写死的正则, 需要根据父组件传进来的值生成正则表达式
      const regExp = new RegExp(this.rule)   
      // 这是正则的构造函数, 传入一个字符串即可   
      // var a = /this.rule/ 这样不行, 因为他会以为 this.rule 是需要匹配的字符串, 不会作为变量
      // 为什么要转换成正则对象, 不能直接用吗
      // 只有转换成正则对象, 才会有 test 的方法
      // 一个字符串是没有这种验证的方法的

      // console.log(regExp);
      // console.log(event.target.value);
      this.isValid = regExp.test(event.target.value);
      // console.log(isValid);
      // 这个值 true / false 值放到 data 先存起来

      // 这里checkValue之后应该主动将数据传递到父组件
      // 第二个参数开始就是要传递的数据,可以被父组件的函数接受到
      this.$emit('valueChange', event.target.value)
      
    },
    showAlert() {
      // 这里是失去焦点时候触发的函数
      // 需要判断, 如果 this.isValid 已然为 false
      // 证明不合法, 就要弹窗
      // 如果合法, 什么都不做
      if (!this.isValid) {
        // 这里的处理可以根据自己的喜好, 提醒用户即可
        // 正常这里的提醒方式, 是产品跟设计共同决定的
        alert(this.errMsg)
      }
    }
  }
}
</script>

<style lang="less" scoped>
  // h1 {
  //   color: green;
  // }
  .input-wrapper {
    padding: 5.556vw;
    padding-top: 0;
    input {
      width: 100%;
      height: 10.556vw;
      box-sizing: border-box;
      background: #fff;
      border: none;
      /* 因为 vw 都是经过换算, 太小的数字容易被忽略,
      有一些浏览器会显示不出来
      一般小于 10px 的情况下,我们都会直接写像素 */
      border-bottom: 1px solid #666;
      outline: none;
    }
    .err {
      border-color: red;
    }
    .success {
      border-color: #666
    }
  }
  
</style>