import Vue from 'vue'
// 我们在这里引入组件, 使用的是相对路径
// ./xxx/xxx/xxx
// vue-cli 为了避免这种无限嵌套的情况下
// 容易混乱, 配置了一个快捷方式 @ 这个符号如果用在路径当中
// 代表的就是 src 根目录
// import App from './App.vue'
import App from '@/App.vue'
import router from './router'

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
